@import url(https://fonts.googleapis.com/css2?family=Inconsolata&display=swap);
body{margin:0;font-family:Inconsolata,monospace;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body{height:100%;width:100%;scroll-behavior:smooth}#root{display:flex;height:100%;flex-direction:column;align-items:center;justify-content:space-between;--bg-color: black;--font-color: lightslategray;--hl-color: orange;--fg-color: #292f34;color:var(--font-color);background-color:var(--bg-color)}button{background-color:var(--bg-color);color:var(--font-color);border:2px solid var(--font-color);padding:10px;font-family:Inconsolata,monospace;font-weight:700;margin:10px;cursor:pointer}button.mini{font-size:12pt;border:none;padding:5px;margin:0}button.mini.selected{color:var(--hl-color)}button:hover{color:var(--hl-color);border-color:2px solid var(--fg-color)}a{color:var(--font-color);text-decoration:none;transition:color .1s ease}a span{font-size:smaller;margin:2.5px}a:hover{color:var(--hl-color)}.hidden{opacity:0;pointer-events:none}
table{font-size:16pt;font-weight:800}table button{font-size:21pt}
.test{display:flex;flex-direction:column;font-weight:700;width:50%}.timer{font-size:21pt;margin:5px;color:var(--hl-color)}.box{font-size:21pt;overflow:hidden;height:93px;display:flex;flex-wrap:wrap;align-items:center;-webkit-user-select:none;-ms-user-select:none;user-select:none}.box .word{position:relative;margin:0px 5px 2px 5px}.box .word.wrong{-webkit-text-decoration:2px underline var(--hl-color);text-decoration:2px underline var(--hl-color)}.box .typedWord{position:absolute;top:0;left:0}.box .right{color:var(--fg-color)}.box #active{position:relative;width:-webkit-max-content;width:-moz-max-content;width:max-content}.box span.wrong{color:var(--hl-color) !important}.box #caret{transition:left .1s ease;margin-left:-7.29165px;position:absolute;color:var(--hl-color)}.box #caret.blink{animation:blink 1.5s infinite 1s}@keyframes blink{0%,100%{opacity:1}50%{opacity:0}}
header,footer{margin:50px 0px;display:flex;width:50%;justify-content:space-between;align-items:center;font-size:12pt;font-weight:700}header .buttons,footer .buttons{display:flex;flex-direction:column;align-items:flex-end}.bottom-area{width:50%;justify-content:center}.bottom-area .hint{display:flex;align-items:center;justify-content:center}.bottom-area .hint kbd{background-color:var(--font-color);color:var(--bg-color);padding:2.5px 5px;margin:10px;border-radius:3px;font-size:9pt}.bottom-area footer{width:100%}.brand{font-size:21pt;align-self:center}.mkbhd{--bg-color: #171717 !important;--font-color: #ededed !important;--hl-color: #da0037 !important;--fg-color: #444444 !important}.coral{--bg-color: #fdd2bf !important;--font-color: #df5e5e !important;--hl-color: #492f10 !important;--fg-color: #e98580 !important}.ocean{--bg-color: #dddddd !important;--font-color: #125d98 !important;--hl-color: #f5a962 !important;--fg-color: #3c8dad !important}.azure{--bg-color: #383e56 !important;--font-color: #eedad1 !important;--hl-color: #f69e7b !important;--fg-color: #d4b5b0 !important}.forest{--bg-color: #334443 !important;--font-color: #fff8df !important;--hl-color: #c6ffc1 !important;--fg-color: #34656d !important}
